/* Styling for the App component nav element 
and Course component course-nav div */

html{
  min-height: 100%;
  height: 100%;
}

body{
  margin: 0;
  font-family: Arial;
  background: linear-gradient(-40deg,#6e127a,#000) fixed;
  height: 100%;
  min-height: 100%;
  /* background: linear-gradient(-40deg,#6e127a,#000); */
  /* height: 100%; */
  /* background-repeat: no-repeat;  */
  /* background-size: cover; */
  /* background-color: rgb(0, 0, 0); */
}

#root{
  height: 100%;
  min-height: 100%;
}

.App{
  height: 100%;
}

.homepage{
  text-align: center;
}

.container {
  text-align: center;
  width: 500px;
  border: px solid rgb(111,41,97);
  background-color: #00000017;
  border-radius: .5em;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  margin: 1em;
}

.App nav{
  background-size: cover;
  /* background-size: 100% auto; */

  background: #330039;
  min-width: 25%;
  text-align: left;
  padding: 10px;

}

.songs-nav{
  background: rgba(88, 15, 98, 0.07);;
  min-width: 25%;
  text-align: center;

}
  
/* Styling for App component nav element anchor 
tag and Course component nav element anchor tag */
.App nav a{
  padding: 20px 20px;
  font-size: 26px;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
}

.songs-nav a{
  padding: 20px 20px;
  font-size: 26px;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
}

.App nav a:hover {
  background: #9c09ac23;
  padding-block: 10px;
}
  
/* Styling for Course component 
course-nav div anchor tag */
.songs-nav a{
  color: rgb(255, 255, 255);

}
  
